
// -------------------------------[ CONTACT SECTION ]------------------------------- //

export default function Contact() {
    return (
        <div className="contact-section">
            <div className="container">
                <div className="row align-items-center mt-5">
                    <div className="col-12 col-sm-6 d-flex flex-column justify-content-center text-center gap-5">
                        <div >
                            <h3 className="mb-3">Horarios de Atención</h3>
                            <h5>LUNES-VIERNES</h5>
                            <h6>9:30 a 13:30 horas / 14:30 a 18:00 horas</h6>
                        </div>
                        <div >
                            <h3 className="mb-3">Atención Telefónica</h3>
                            <h5>LUNES-VIERNES</h5>
                            <h6>8:30 a 19:00 horas</h6>
                        </div>
                        <div>
                            <h3 className="mb-3">No hay Atención</h3>
                            <h5 className="mb-3">SÁBADO, DOMINGO, FERIADOS Y/O FESTIVOS</h5>
                        </div>
                        <div className="mb-4 align-self-center" style={{ maxWidth: 320 }}>
                            <ul className="text-start">
                                <li>
                                    <p className="lead" >
                                        <strong>
                                            Tu primera consulta es Gratuita
                                        </strong>
                                    </p>
                                </li>
                                <li>
                                    <p className="text-start lead" >
                                        <strong>
                                            Estarás constantemente informado
                                            del estado de tu proceso, procedimiento o trámite
                                        </strong>
                                    </p>
                                </li>
                            </ul>
                        </div>
                    </div>
                    <div className="col-12 col-sm-6 d-flex justify-content-center">
                        <div className="card rounded-4 py-2" style={{ borderColor: '#ea7a69' }}>
                            <div className="card-body" >
                                <div className="container">
                                    <h2 className="mb-4" >¿Quieres <span style={{ color: '#ea7a69' }}>contactarme?</span></h2>
                                    <p className="card-title text-start lead mb-4">
                                        Por favor envíame tu consulta con tus datos
                                        llenando el siguiente formulario y yo te
                                        contactaré de vuelta tan pronto como pueda.
                                    </p>
                                    <form target="_blank" action="https://formsubmit.co/abogadabaviera@gmail.com" method="POST">
                                        <div className="form-group ">
                                            <div className="form-row">
                                                <div className="col mb-2">
                                                    <input type="text" name="name" className="form-control shadow-none rounded-3" placeholder="Tu nombre completo" required />
                                                </div>
                                                <div className="col mb-2">
                                                    <input type="email" name="email" className="form-control shadow-none rounded-3" placeholder="Tu dirección de email" required />
                                                </div>
                                                <div className="col mb-2">
                                                    <input type="tel" name="phone" className="form-control shadow-none rounded-3" placeholder="Tu múmero de teléfono" required />
                                                </div>
                                            </div>
                                        </div>
                                        <input type="hidden" name="_subject" value="Nueva consulta."></input>
                                        <div className="form-group mb-2">
                                            <textarea placeholder="Escribe tu consulta" className="form-control shadow-none rounded-3 " name="message" rows="8" required style={{ resize: 'none' }}></textarea>
                                        </div>
                                        <button type="submit" className="btn btn-dark rounded-3 border-0" style={{ backgroundColor: '#ea7a69' }} >Enviar consulta</button>
                                    </form>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    );
}
